<template>
  <div>
    <nav-banner toggle='about'></nav-banner>
    <div class="about">
      <div>
        <h1 class="animated fadeInUp">联系方式</h1>
        <a class="animated flipInX" href="tel:17665288800">17665288800</a>
        <a class="animated flipInX" href="mailto:baidonbo@qq.com">baidonbo@qq.com</a> 
        <div class="project">
          <h1 class="animated fadeInUp ">我的项目</h1>
          <a class="animated flipInX" href="#">白豆瓣</a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import navBanner from '../components/navBanner';
export default {
  name: 'about',
  data () {
    return {
    }
  },
  components:{ navBanner },
}
</script>

<style lang='less' scoped>
.about {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(31, 155, 177, 0.618);
  display: flex;
  justify-content: center;
  letter-spacing: .5rem;
  h1 {
    // text-align: center;
    margin-top: 5rem;
  }
  a {
    margin-top: 2rem;
    display:block;
    font-size:2rem;
    background: url(../assets/contactIcon.png) no-repeat;
    padding-left: 2rem;
    background-size:2rem;
    
  }
 a:first-child {
    background-position: 0 0;
  }
 a:nth-of-type(2) {
    background-position: 0 105%;
  }
  .project > a{
    background-image: url();
  }
  
}
</style>



