<template>
  <div>
    
    <nav-banner toggle='home' :showImg=showImg @changImgs='changImg($event)'></nav-banner>

    <div class="wrapper w1" v-show='showImg==0' ref='r11'>
      <transition name='header'
      :duration="{ enter: 5000, leave: 5000 }"
      enter-active-class="animated bounceInDown" 
      
      >
      <!-- leave-active-class="animated bounceOutUp"  -->
        <div class="header" v-if='showImg==0'>
          <table>
              <td>
                  <ul class="left">
                    <li>姓名：柏东波</li>
                    <li>性别：男</li>
                    <li>籍贯：湖南</li>
                  </ul>
              </td>
              <td>
                <a href="http://baidonbo.github.io">
                    <img src="@/assets/idimg.jpg" alt="我的个人照片" align='middle' >
                </a>
              </td>
              <td>
                  <ul class="right">
                    <li><a href="tel:17665288800">电话：17665288800</a></li>
                    <li><a href="mailto:baidonbo@qq.com">邮箱：909211913@qq.com</a></li>
                    <li>出生年月：1989年8月</li>
                  </ul>
              </td>
          </table>
        </div>
      </transition>
    </div>
 
    <div class="wrapper w2" v-show='showImg==1' ref='r22'>
      <transition name='education'
      enter-active-class="animated zoomInRight"
      >
        <div class="education" v-if='showImg==1'>
            <h1>教育背景</h1>
            <table>
              <tr v-for='value in eduBackgrounds' :key="value.id">
                <td> {{ value.school}} </td>
                <td> {{ value.profession}} </td>
                <td> {{ value.year}} </td>
              </tr>
            </table>
        </div>
      </transition>
    </div>

    <div class="wrapper w3" v-show='showImg==2' ref='r33'>
      <transition name='skill'
      >
       <!-- enter-active-class="animated bounceInUp" -->
        <div class="skill" v-if='showImg==2'>
            <h1>技能特长</h1>
            <ul>
              <li v-for='value in skillArr' :key="value.id">{{ value }}</li>
            </ul>
        </div>
      </transition>
    </div>

    <div class="wrapper w4" v-show='showImg==3' ref='r44' >
      <transition name='work'
       :duration="{ enter: 2000, leave: 1000 }"
       enter-active-class="animated bounceInLeft"
       leave-active-class="animated zoomOutUp"
       >
        <div class="work" v-if='showImg==3'>
          <h1>工作经历</h1>
          <ul>
            <li v-for='(value) in workExperience' @click='showCont=!showCont' :key="value.id">
                  <table>
                  <td>{{ value.company }}</td>
                  <td>{{ value.job }}</td>
                  <td>{{ value.inservice }}</td>
                </table>
                <p v-html='value.content' v-show='showCont'></p>
            </li>
            
          </ul>
        </div>
      </transition>
    </div>
    
  </div>
</template>

<script>
import '@/public/home.less';
import navBanner from '../components/navBanner';
import datas from '../components/datas'

export default {
  name: 'home',
  components:{ navBanner },
  data: function(){
    return{
      // 工作内容显示控制
      showCont: false,
      //每个循环值
      showImg: 0,
      //setInterval控制器
      timer:null,
      navButton:datas.navButton,
      eduBackgrounds: datas.eduBackgrounds,
      skillArr:datas.skillArr,
      workExperience: datas.workExperience,
    }
  },
  methods: {
    //鼠标滚轮事件兼容处理
    getWheelDelta(event) {
      if (event.wheelDelta) {
        // 第一次调用之后惰性载入
        this.getWheelDelta = event => event.wheelDelta;
  
        // 第一次调用使用
        return event.wheelDelta;
      } else {
        // 兼容火狐
        this.getWheelDelta = event => -event.detail;
        return -event.detail;
      }
    },
    // 截流函数，method 回调函数，context 上下文，delay 延迟函数，
    // 这里没有提供是在延迟时间开始还是结束的时候执行回调函数的选项，
    // 直接在延迟时间开始的时候执行回调
    //handleMouseWheel = this.throttle(this.myScroll, this, this.DELAY, true);
    throttle(method, context, delay) {
      let wait = false;
      return function() {
        if (!wait) {
          method.apply(context, arguments);
          wait = true;
          setTimeout(() => {
            wait = false;
          }, delay);
        }
      };
    },
    
    nextImg(){
      this.showImg++;
      if (this.showImg > this.navButton.length-1){
        this.showImg = 0
      }
    },
    preImg(){
      this.showImg--;
      if (this.showImg < 0){
        this.showImg = this.navButton.length-1
      }
    },
    stop(){
      clearInterval(this.timer);
      this.timer = null;
    },
    auto(){
      clearInterval(this.timer);
      this.timer = setInterval(() => {
        this.nextImg()
      }, 5000);
    },
    changImg(index){
      clearInterval(this.timer);
        this.showImg = index;
    },
    //鼠标滚动函数
    myScroll(e){
      clearInterval(this.timer);
      // console.log(e.wheelDelta)
      let delta = this.getWheelDelta(e);
      if (delta < 0){
        this.nextImg()
      }else{
        this.preImg();
      }
    },
    // 触屏事件
    handleTouchEnd(event) {
      event.preventDefault();
      clearInterval(this.timer);
      let endY = event.changedTouches[0].pageY;
      if (endY - this.startY < 0) {
        // 手指向上滑动，对应页面向上滚动
        this.preImg();
      } else {
        // 手指向下滑动，对应页面向下滚动
        this.nextImg();
      }
    },
  },
  computed:{
  },
  mounted(){
    //自动运行
    this.$nextTick(()=>{
      clearInterval(this.timer);
      this.timer = setInterval(() => {
        this.nextImg()
      }, 5000);
    });

    document.addEventListener('dblclick', () => {
      if (this.timer ===null){
        this.auto();
      }else{
        this.stop();
      }
    })

    // 手指接触屏幕
    document.addEventListener('touchstart', event => {
      event.preventDefault();
      this.startY = event.touches[0].pageY;
    });
    //手指离开屏幕
    document.addEventListener('touchend', this.handleTouchEnd);
    // 阻止触屏长按默认事件
    document.addEventListener('touchmove', event => {
      event.preventDefault();
    });

    // 截流函数
    let mouseScroll =this.throttle(this.myScroll, this, 900);
    //挂载鼠标滚轮事件
    if (navigator.userAgent.toLowerCase().indexOf('firefox') === -1) {
      document.addEventListener('mousewheel', mouseScroll);
    } else {
      document.addEventListener('DOMMouseScroll', mouseScroll);
    };

    console.log(`
    欢迎光临我的主页，恭喜发财，大吉大利。
    本页面支持触摸屏控制，双击暂停动画，再次双击循环。
    `)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>


