<template>
<div class="nav-wrapper">
  <div class="nav">
    <h1>
      <a href="https://baidonbo.github.io">
        <img src="@/assets/idimg.jpg" class="logo" alt="我的头像">
        <span title="双击暂停再次循环">柏生</span>
      </a>
    </h1>
      <ul>
        <li v-if='toggle=="home"'><router-link to='/about'>其它</router-link></li>
        <li v-if='toggle=="about"'><router-link to='/'>返回</router-link></li>
        <li v-for='(value,index) in navButton'  :key="value.id" :class='{"active":(navButton.length-index-1)===showImg}' @click="$emit('changImgs',navButton.length-index-1)">
          <a href="#" >{{ value }}</a>
        </li>
      </ul>
  </div>
</div>
</template>

<script>
import datas  from '../components/datas'
export default {
  name: 'nav-banner',
  data(){
    return{
      navButton:datas.navButton,
      switchValue01:true,
      switchValue02:false,
    } 
  },
  props:['toggle','showImg'],
}
</script>

<style lang='less'>
  // 导航栏
  .nav-wrapper {
    position: relative;
    z-index: 5;
    top: 0;
    left: 0;
    background: rgb(238, 235, 235);
    width: 100%;
    .nav {
      background: rgb(238, 235, 235);
      margin: 0 auto;
      max-width: 1200px;
      height: 3.5rem;
      line-height: 3.5rem;
      h1 {
        line-height: 3.5rem;
        float: left;
        a {
          line-height: 3.rem;
          transition:all .4s ease;
          .logo {
            width: 2.5rem;
            height: 2.5rem;
            border-radius: 48%;
            vertical-align: top;
            margin: .5rem 0 0 1rem;
            transition:all .4s ease;
          }
          //图片后面的文字
          // span {
          //   position: relative;
          //   left: 0rem;
          //   top: 0;
          // }
        }
        a:hover {
          text-shadow:.05rem .05rem .12rem rgb(166, 166, 166);
        }
      }
      li {
        float: right;
        margin: 0 1rem;
      }
      li.active a{
        color: rgb(22, 222, 222);
      }
      li:first-child > a {
        font-size: 0.5rem;
        color: rgb(18, 102, 212);
      }
    }
  }
</style>



